<template>
  <div class="sidebar">
      <connected-user></connected-user>
      <search></search>
      <channels></channels>
  </div>
</template>

<script>
import ConnectedUser from './ConnectedUser';
import Search from './Search'
import Channels from './Channels';
export default {
    name:'sidebar',
    components:{
        ConnectedUser,
        Search,
        Channels,
    }
}
</script>

<style scoped>
    .sidebar{
        position: fixed;
        display: flex;
        flex-flow: column nowrap;
        width: 240px;
        padding: 10px;
        top: 54px;
        left: 0;
        bottom: 0;
        background-color: white;
        padding-top: 70px;
        z-index: 1;
        border-right: 0.3px solid #14afb4;
    }
</style>


