<template>
  <div class="container">
    <div class="ui list">
      <div class="item">
        <div class="right floated content">
          <h5 class="ui middle header">
            <i class="bell outline icon"></i>
          </h5>
        </div>
        <div class="content">
          <div class="ui middle header text">DOANTOTNGHIEaaaaaaP</div>
        </div>
      </div>
      <div class="item">
        <div class="left floated content">
          <i class="circle icon"></i>
        </div>
        <div class="content">
          <div class="username text">Thai Pham</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "connected-user",
};
</script>

<style scoped>
.container {
  position: absolute;
  background-color:#14afb4;
  top: 0;
  width: 100%;
  bottom: 0;
  left: 0;
  height: 60px;
  padding: 8px 16px;
}
.ui .header {
  color: white !important;
  background: none;
    border: none;
    box-shadow: none;
}
.item {
  height: 25px;
}
.bell.outline.icon {
  display: contents !important;
  padding-top: 2px !important;
  cursor: pointer;
}
.circle.icon {
  font-size: 0.8em !important;
  color: rgb(51, 255, 0) !important;
}
.content {
  color: white;
  display: flex;
}
.container:hover {
  background-color: #017D89;
  font-weight: bold;
}
.text {
  width: 180px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  position: absolute;
}
</style>


