<template>
  <div class="ui middle aligned center aligned grid login_container">
    <div class="column">
      <h2 class="ui teal image header">
        <!-- <img src="assets/images/logo.png" class="image" /> -->
        <div class="content">{{dataShow.title}}</div>
      </h2>
      <form class="ui large form">
        <div class="ui stacked segment">
          <div class="field">
            <div class="ui left icon input">
              <i class="user icon"></i>
              <input type="text" name="email" :placeholder="dataShow.email" v-model="message" />
            </div>
          </div>
          <div class="field">
            <div class="ui left icon input">
              <i class="lock icon"></i>
              <input
                type="password"
                name="password"
                :placeholder="dataShow.password"
                v-model="password"
              />
            </div>
          </div>
          <!-- <div class="ui fluid large teal submit button" @click.prevent="goToDashboard">{{dataShow.login}}</div> -->
          <button class="ui fluid large teal button" @click="checkLogin">{{dataShow.login}}</button>
        </div>

        <div class="ui error message"></div>
      </form>
      <div class="ui fluid">
        <button class="ui facebook button">
          <i class="facebook icon"></i>
          Facebook
        </button>
        <button class="ui google plus button">
          <i class="google icon"></i>
          Google
        </button>
      </div>
      <div class="ui message">
        {{dataShow.forgot_password}}
         <button @click="checkRegister">{{dataShow.register}}</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      dataShow: {
        title: "Đăng nhập",
        email: "Địa chỉ gmail",
        password: "Mật khẩu",
        forgot_password: "Quên mật khẩu ? ",
        register: "Đăng kí",
        login: "Đăng nhập"
      }
    };
  },
  methods: {
    goToDashboard() {},
    checkLogin: function() {
      this.$emit("setLogin");
    },
    checkRegister: function() {
      this.$emit("setRegister");
    }
  }
};
</script>

<style scoped>
.login_container {
  margin-top: 40px;
}
.column {
  max-width: 450px;
}
</style>>


