<template>
  <!-- header header  -->
  <div class="header is_stuck">
    <nav class="navbar top-navbar navbar-expand-md navbar-light">
      <!-- Logo -->
      <div class="navbar-header">
        <a class="navbar-brand" href="index.html">
          <router-link to="/" class="navbar-brand invisible-xs" exct>
            <img id="small-logo" src="../../images/logo-small.png" alt="Excello" />
            <img id="full-logo" src="../../images/logo-full.png" alt="Excello" />
          </router-link>
        </a>
      </div>
      <!-- End Logo -->
      <div class="navbar-collapse">
        <sidebar-collapse></sidebar-collapse>
        <!-- User profile and search -->
        <ul class="navbar-nav my-lg-0">
          <!-- Search -->
          <li class="nav-item hidden-sm-down search-box">
            <a
              class="nav-link hidden-sm-down text-muted"
              href="javascript:void(0)"
              @click="searchClick"
              :class="{'search-open': isSearch, '': !isSearch }"
            >
              <i class="ti-search"></i>
            </a>

            <form class="app-search" :class="{ 'form-inline': isSearch, '': !isSearch }">
              <input type="text" class="form-control" placeholder="Search here" />
              <a class="srh-btn" @click="isSearch = !isSearch">
                <i class="ti-close"></i>
              </a>
            </form>
          </li>
          <!--Languages-->
          <li class="nav-item dropdown">
            <a
              class="nav-link dropdown-toggle text-muted"
              href="#"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >{{ $t("nav-header.language.name") }}</a>
            <div class="dropdown-menu dropdown-menu-right animated zoomIn">
              <ul class="dropdown-user">
                <li>
                  <a @click="callSetLangActions('vi')" style="cursor:pointer;">{{ $t("nav-header.language.vietnamese") }}</a>
                </li>
                <li>
                  <a @click="callSetLangActions('en')" style="cursor:pointer;">{{ $t("nav-header.language.english") }}</a>
                </li>
              </ul>
            </div>
          </li>

          <!--End Languages-->
          <!-- Messages -->
          <li
            class="nav-item dropdown"
            :class="{'show': isMessage, '': !isMessage }"
            @click="handleMessage"
          >
            <a
              class="nav-link dropdown-toggle text-muted"
              href="#"
              id="2"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              <i class="ti-plus"></i>
            </a>
            <div
              class="dropdown-menu dropdown-menu-right animated zoomIn"
              aria-labelledby="2"
              :class="{'show': isMessage, '': !isMessage }"
              @click="handleMessage"
            >
              <ul class="dropdown-user">
                <li>
                  <a href="#">{{ $t("nav-header.btn-add.add-project") }}</a>
                </li>
                <li>
                  <a href="#">{{ $t("nav-header.btn-add.add-task") }}</a>
                </li>
              </ul>
            </div>
          </li>
          <!-- End Messages -->
          <!-- Notification -->
          <li
            class="nav-item dropdown"
            :class="{'show': isNotification, '': !isNotification }"
            @click="handleNotification"
          >
            <a
              class="nav-link text-muted text-muted"
              href="/#/notifications"
              aria-haspopup="true"
              aria-expanded="false"
            >
              <i class="fa fa-bell"></i>
              <div class="notify">
                <span class="heartbit"></span>
                <span class="point"></span>
              </div>
            </a>
          </li>
          <!-- End Notification -->
          <!-- Profile -->
          <li
            class="nav-item dropdown"
            :class="{'show': isUserInfo, '': !isUserInfo }"
            @click="handleUserInfo"
          >
            <a
              class="nav-link dropdown-toggle text-muted"
              href="#"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              <img src="../../images/users/5.jpg" alt="user" class="profile-pic" />
            </a>
            <div
              class="dropdown-menu dropdown-menu-right animated zoomIn"
              :class="{'show': isUserInfo, '': !isUserInfo }"
              @click="handleUserInfo"
            >
              <ul class="dropdown-user">
                <li>
                  <a href="#">
                    <i class="ti-user" style="margin-right:10px;"></i>{{ $t("nav-header.user.edit-profile") }}
                  </a>
                </li>
                <li>
                  <a href="#">
                    <i class="fa fa-key" style="margin-right:10px;"></i>{{ $t("nav-header.user.change-password") }}
                  </a>
                </li>
                <li>
                  <a href="#">
                    <i class="fa fa-power-off" style="margin-right:10px;"></i>{{ $t("nav-header.user.logout") }}
                  </a>
                </li>
              </ul>
            </div>
          </li>
        </ul>
      </div>
    </nav>
  </div>
</template>

<script>
import SidebarCollapse from "./SidebarCollapse.vue";
export default {
  data() {
    return {
      isSearch: false,
      isOpen: false,
      clickable: true,
      isNotification: false,
      isMessage: false,
      isUserInfo: false
    };
  },
  components: {
    SidebarCollapse
  },
  methods: {
    toggle(e) {
      //this.isOpen = !this.isOpen
      e.target.parentElement.classList.toggle("close");
    },
    searchClick(e) {
      this.isSearch = !this.isSearch;
    },
    handleMessage(e) {
      this.isMessage = !this.isMessage;
    },
    handleNotification(e) {
      this.isNotification = !this.isNotification;
    },
    handleUserInfo(e) {
      this.isUserInfo = !this.isUserInfo;
    },
    callSetLangActions(language) {
      this.$store.dispatch('setLang', language);
    }
  }
};
</script>

<style lang="scss">
.color-icon-header {
  color: white;
}
.form-inline {
  display: block !important;
}
.notification-show + .dropdown-menu,
.message-show + .dropdown-menu,
.language-show + .dropdown-menu {
  display: block;
}
.header .top-navbar .navbar-header .navbar-brand {
  height: 35px;
  max-width: 250px;
}

.navbar-brand #small-logo,
.mini-sidebar .navbar-brand #full-logo {
  display: none;
}
.mini-sidebar .navbar-brand #small-logo {
  display: inline;
}
#small-logo,
#full-logo {
  width: auto;
  height: 35px;
}
.header.is_stuck {
  min-height: 54px;

  z-index: 21;
}
.header.is_stuck .navbar {
  position: fixed;
  width: 100%;
  background: white;
  box-shadow: 1px 0 5px rgba(0, 0, 0, 0.1);
}

.navbar-header {
  background: white;
}
</style> 


