<template>
  <div class="ui middle aligned center aligned grid login_container">
    <div class="column">
      <h2 class="ui teal image header">
        <!-- <img src="assets/images/logo.png" class="image" /> -->
        <div class="content">{{dataShow.title}}</div>
      </h2>
      <form class="ui large form">
        <div class="ui stacked segment">
          <div class="field">
            <div class="ui left icon input">
              <i class="user icon"></i>
              <input type="text" name="email" :placeholder="dataShow.email" v-model="message"/>
            </div>
          </div>
          <div class="field">
            <div class="ui left icon input">
              <i class="lock icon"></i>
              <input type="password" name="password" :placeholder="dataShow.password" v-model="password" />
            </div>
          </div>
          <div class="field">
            <div class="ui left icon input">
              <i class="lock icon"></i>
              <input type="password" name="password" :placeholder="dataShow.password_confirm" v-model="password_confimation" />
            </div>
          </div>
          <div class="ui fluid large teal submit button">{{dataShow.register}}</div>
        </div>

        <div class="ui error message"></div>
      </form>

      <div class="ui message">
        <button @click="checkRegister">{{dataShow.login}}</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name:'register',
  data(){
    return{
        dataShow:{
          title:'Đăng kí',
          email:'Địa chỉ gmail',
          password:'Mật khẩu',
          password_confirm:'Xác nhận lại mật khẩu',
          register:'Đăng kí',
          login:'Đăng nhập'
        }
    }
  },
  methods:{
    checkRegister: function() {
      this.$emit("setRegister");
    }
  }
};
</script>

<style scoped>
.login_container {
  margin-top: 40px;
}
.column {
  max-width: 450px;
}
</style>


