import Vue from 'vue'
import Vuex from 'vuex'
import app from './main'

Vue.use(Vuex)

const store = new Vuex.Store({
  mutations : {
    SET_LANG (state, payload) {
      app.$i18n.locale = payload
    }
  },
  actions : {
    setLang({commit}, payload) {
      commit('SET_LANG', payload)
    }
  }
});
export default store


