<template>
  <div class="channels">
    <div class="chanels_container" style="margin-top:0px">
      <h4 class="ui left aligned header">Channels</h4>
      <div
        class="add_icon"
        @click="AddChannel()"
        data-inverted
        data-tooltip="Thêm"
        data-position="bottom right"
      >
        <i class="plus circle icon"></i>
      </div>
    </div>

    <div
      class="item"
      v-bind:class="{active:isSelectedChannel===index}"
      v-for="(channel,index) in this.list_channel"
      :key="index"
      @click="choose_channel(index)"
    >
      <div class="left floated content">
        <i v-if="channel.isLockChannel" class="lock icon"></i>
        <i v-else class="hashtag icon"></i>
      </div>
      <div class="content">
        <div class="channel_name">{{channel.name}}</div>
      </div>
    </div>

    <div class="chanels_container">
      <h4 class="ui left aligned header">Direct Messages</h4>
      <div class="add_icon" data-inverted data-tooltip="Thêm" data-position="bottom right">
        <i class="plus circle icon"></i>
      </div>
    </div>

    <div
      class="item"
      v-bind:class="{active:isSelectedUser===index}"
      v-for="(user,index) in this.list_user"
      :key="index"
      @click="choose_user(index)"
    >
      <div class="left floated content">
        <i v-if="user.isActive" class="circle icon"></i>
        <i v-else class="circle outline icon"></i>
      </div>
      <div class="content">
        <div class="channel_name username">{{user.name}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "channels",
  data() {
    return {
      isSelectedChannel: 0,
      isSelectedUser: -1,
      list_channel: [
        {
          id: "1",
          name: "datn",
          isLockChannel: true
        },
        {
          id: "2",
          name: "nmcnpm",
          isLockChannel: false
        },
        {
          id: "3",
          name: "webnc",
          isLockChannel: false
        },
        {
          id: "2",
          name: "nmcnpm",
          isLockChannel: false
        },
        {
          id: "3",
          name: "webnc",
          isLockChannel: false
        }
      ],
      list_user: [
        {
          id: "1",
          isActive: true,
          name: "Tham Tran"
        },
        {
          id: "2",
          name: "Thai Le",
          isActive: false
        },
        {
          id: "3",
          name: "nqthach",
          isActive: false
        },
        {
          id: "3",
          name: "nqthach",
          isActive: false
        },
        {
          id: "3",
          name: "nqthach",
          isActive: true
        },
        {
          id: "3",
          name: "nqthach",
          isActive: false
        },
        {
          id: "3",
          name: "nqthach",
          isActive: false
        },
        {
          id: "3",
          name: "nqthach",
          isActive: true
        },
        {
          id: "3",
          name: "nqthach",
          isActive: false
        },
        {
          id: "3",
          name: "nqthach",
          isActive: false
        },
        {
          id: "3",
          name: "nqthach",
          isActive: true
        },
        {
          id: "3",
          name: "nqthach",
          isActive: false
        },
        {
          id: "3",
          name: "nqthach",
          isActive: false
        },
        {
          id: "3",
          name: "nqthach",
          isActive: true
        },
        {
          id: "3",
          name: "nqthach",
          isActive: false
        },
        {
          id: "3",
          name: "nqthach",
          isActive: false
        },
        {
          id: "3",
          name: "nqthach",
          isActive: true
        },
        {
          id: "3",
          name: "kkkkkkkkkkkkkkkkkkkkkkkkkkkkkkkkkkk",
          isActive: false
        }
      ]
    };
  },
  methods: {
    choose_channel(index) {
      this.isSelectedChannel = index;
      this.isSelectedUser = -1;
    },
    choose_user(index) {
      this.isSelectedUser = index;
      this.isSelectedChannel = -1;
    },
    AddChannel() {
      this.list_channel.push({
        id: "3",
        name: "a",
        isLockChannel: false
      });
    }
  }
};
</script>

<style scoped>
.header {
  box-shadow: none !important;
  background: none !important;
}
.channels {
  position: absolute;
  left: 0;
  right: 0;
  top: 100px;
  bottom: 0;
  width: 100%;
  margin-top: 25px;
  padding-bottom: 8px;
  overflow: hidden;
}
.channels:hover {
  overflow-y: overlay;
}
.chanels_container {
  display: flex;
  width: 216px;
  height: 30px;
  font-weight: bold;
  position: relative;
  width: 100%;
  margin-top: 25px;
}
/* .chanels_container:hover {
  background-color: #1b9da9;
} */
.ui.header {
  position: absolute;
  padding-top: 6px;
  left: 30px;
  font-size: 16px;
}
.add_icon {
  position: absolute;
  left: 202px;
  padding-top: 3px;
  cursor: pointer;
  width: 27px;
  height: 100%;
}
.add_icon i {
  font-size: 1.5em !important;
      position: absolute;
    height: 100%;
    top: -2px;
}
.item {
  display: flex;
  padding: 4px 7px;
  font-size: 16px;
  cursor: pointer;
}
.left.floated.content {
  font-size: 0.75em;
}
.item:hover {
  background-color: white;
  color: #14afb4;
  border-top: 1.5px solid #14afb4;
  border-bottom: 1.5px solid #14afb4;
}
.active {
  background-color: #14afb4;
  color: white;
}
.circle.icon {
  color: #33ff00 !important;
  font-size: 11px;
}
.circle.outline.icon {
  color: black !important;
}
.plus.circle.icon {
  color: black !important;
}
.username {
  width: 160px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.content {
  padding-left: 10px;
}
.item .icon{
  padding-top: 3px !important;
}
</style>



