<template>
  <div id="app">
    <home v-if="isLogin" v-on:setLogin="setLogin"></home>
    <div v-else>
      <register v-if="isRegister" v-on:setRegister="setRegister"></register>
      <login v-else v-on:setLogin="setLogin" v-on:setRegister="setRegister"></login>
    </div>
  </div>
</template>

<script>
import Home from "./Home.vue";
import Login from "./pages/Login.vue";
import Register from "./pages/Register";

export default {
  data() {
    return {
      isLogin: false,
      isRegister: false,
      
    };
  },
  components: {
    home: Home,
    login: Login,
    Register,
  },
  methods: {
    setLogin: function() {
      this.isLogin = !this.isLogin;
    },
    setRegister: function() {
      this.isRegister = !this.isRegister;
    }
  }
};
</script>

<style scoped>
</style>


