<template>
    <!-- toggle and nav items -->
    <ul class="navbar-nav mr-auto mt-md-0">
        <li class="nav-item m-l-10">
            <a
                class="nav-link sidebartoggler text-muted"
                @click="handleSidebarToggle"
            >
                <i class="ti-menu"></i>
            </a>
        </li>
       
    </ul>
</template>

<style scoped>
.color-icon-header{
  color: white;
}
</style>
<script>
    export default{
        data(){
            return{
                clickable: true,
                isMegaDropwon: false
            }
        },
        methods: {
            toggle(e){
                e.target.parentElement.classList.toggle('close')
            },
            handleSidebarToggle(){
                if( this.clickable ){
                    this.clickable = false;
                    document.body.classList.toggle('mini-sidebar');
                    this.clickable = true;
                }
            },
            handleMegaDropwon (e) {
              this.isMegaDropwon = !this.isMegaDropwon;
            }

        }
    }
</script>


